<template>
  <div class="card border-primary">
    <div class="card-body">
      <div class="row">
        <div class="col-12 mb-3">
          <label class="form-label">選擇關卡</label>
          <select class="form-select" v-model="stageId">
            <option v-for="stage in stages" :value="stage.id">
              {{ stage.displayName }}
            </option>
          </select>
        </div>
      </div>
    </div>
    <div class="card-footer">
      <button type="button" class="btn btn-primary" @click="start()" :disabled="!Boolean(stageId)">開始</button>
    </div>
  </div>
</template>

<script lang="ts" setup>
import { ref } from 'vue'
import { useRouter } from 'vue-router'
import stages from '@/assets/stages.json'

const router = useRouter();
const stageId = ref<number>();

async function start() {
  router.push({
    name: 'play',
    query: {
      stage: stageId.value
    }
  });
}
</script>